/**
 * 청약 정보 데이터 모듈
 * 청약 일정, 가점 계산, 경쟁률 데이터
 * 
 * 성능 최적화: 데이터 캐싱 적용
 */

// 데이터 캐시 저장소
const subscriptionCache = new Map();

// 캐시된 데이터 반환 또는 생성
const getCachedData = (key, generator) => {
    if (subscriptionCache.has(key)) {
        return subscriptionCache.get(key);
    }
    const data = generator();
    subscriptionCache.set(key, data);
    return data;
};

// 캐시 무효화
export const clearSubscriptionCache = () => {
    subscriptionCache.clear();
};

// 청약 일정 시뮬레이션 데이터
export const generateSubscriptionSchedule = () => getCachedData('schedule', () => {
    const now = new Date();
    const schedules = [];

    // 다가오는 청약 일정 생성
    const complexNames = [
        '래미안 원베일리', '디에이치 아너힐즈', '힐스테이트 더 운정',
        '롯데캐슬 시그니처', '자이 더 시티', '푸르지오 센트럴시티',
        'e편한세상 테라스', '아크로 리버뷰', '더샵 그랜드',
    ];

    const regions = ['서울', '경기', '인천', '부산', '대구'];
    const districts = {
        '서울': ['강남구', '서초구', '송파구', '마포구', '용산구', '성동구'],
        '경기': ['성남시', '고양시', '수원시', '용인시', '화성시', '파주시'],
        '인천': ['연수구', '송도', '청라', '계양구'],
        '부산': ['해운대구', '수영구', '남구', '동래구'],
        '대구': ['수성구', '달서구', '중구'],
    };

    for (let i = 0; i < 15; i++) {
        const startDate = new Date(now);
        startDate.setDate(now.getDate() + Math.floor(Math.random() * 60) + 1);

        const endDate = new Date(startDate);
        endDate.setDate(startDate.getDate() + 5);

        const region = regions[Math.floor(Math.random() * regions.length)];
        const district = districts[region][Math.floor(Math.random() * districts[region].length)];

        const totalUnits = Math.floor(Math.random() * 800) + 200;
        const specialUnits = Math.floor(totalUnits * (Math.random() * 0.3 + 0.2));
        const generalUnits = totalUnits - specialUnits;

        schedules.push({
            id: `sub-${i + 1}`,
            name: complexNames[Math.floor(Math.random() * complexNames.length)],
            region,
            district,
            address: `${region} ${district}`,
            startDate: startDate.toISOString().split('T')[0],
            endDate: endDate.toISOString().split('T')[0],
            announcementDate: new Date(endDate.getTime() + 14 * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
            totalUnits,
            specialUnits,
            generalUnits,
            avgPrice: Math.floor(Math.random() * 15 + 8) * 10000, // 8억~23억
            pricePerPyeong: Math.floor(Math.random() * 3000 + 2000), // 2000~5000만원/평
            expectedCompetition: Math.floor(Math.random() * 50) + 5,
            type: Math.random() > 0.5 ? '분양' : '임대',
            status: startDate > now ? 'upcoming' : 'ongoing',
        });
    }

    return schedules.sort((a, b) => new Date(a.startDate) - new Date(b.startDate));
});

// 청약 가점 계산
export const calculateSubscriptionPoints = ({
    noHouseYears = 0,    // 무주택 기간 (년)
    dependents = 0,       // 부양가족 수
    accountYears = 0,     // 청약통장 가입기간 (년)
}) => {
    // 무주택 기간 점수 (최대 32점)
    const noHousePoints = (() => {
        if (noHouseYears < 1) return 2;
        if (noHouseYears < 2) return 4;
        if (noHouseYears < 3) return 6;
        if (noHouseYears < 4) return 8;
        if (noHouseYears < 5) return 10;
        if (noHouseYears < 6) return 12;
        if (noHouseYears < 7) return 14;
        if (noHouseYears < 8) return 16;
        if (noHouseYears < 9) return 18;
        if (noHouseYears < 10) return 20;
        if (noHouseYears < 11) return 22;
        if (noHouseYears < 12) return 24;
        if (noHouseYears < 13) return 26;
        if (noHouseYears < 14) return 28;
        if (noHouseYears < 15) return 30;
        return 32;
    })();

    // 부양가족 수 점수 (최대 35점)
    const dependentPoints = (() => {
        if (dependents === 0) return 5;
        if (dependents === 1) return 10;
        if (dependents === 2) return 15;
        if (dependents === 3) return 20;
        if (dependents === 4) return 25;
        if (dependents === 5) return 30;
        return 35;
    })();

    // 청약통장 가입기간 점수 (최대 17점)
    const accountPoints = (() => {
        if (accountYears < 1) return 1;
        if (accountYears < 2) return 2;
        if (accountYears < 3) return 3;
        if (accountYears < 4) return 4;
        if (accountYears < 5) return 5;
        if (accountYears < 6) return 6;
        if (accountYears < 7) return 7;
        if (accountYears < 8) return 8;
        if (accountYears < 9) return 9;
        if (accountYears < 10) return 10;
        if (accountYears < 11) return 11;
        if (accountYears < 12) return 12;
        if (accountYears < 13) return 13;
        if (accountYears < 14) return 14;
        if (accountYears < 15) return 15;
        return 17;
    })();

    const totalPoints = noHousePoints + dependentPoints + accountPoints;

    return {
        noHousePoints,
        dependentPoints,
        accountPoints,
        totalPoints,
        maxPoints: 84,
        percentage: Math.round((totalPoints / 84) * 100),
    };
};

