import { useState, useMemo, useCallback, memo } from 'react';
import {
    BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip,
    ResponsiveContainer, Cell
} from 'recharts';
import {
    Home, Calendar, Calculator, TrendingUp, TrendingDown,
    Users, Clock, Award, MapPin, ChevronRight, Filter
} from 'lucide-react';
import ChartContainer from '../components/ui/ChartContainer';
import StatCard from '../components/ui/StatCard';
import Tabs from '../components/ui/Tabs';
import {
    generateSubscriptionSchedule,
    calculateSubscriptionPoints,
    getRegionalCompetitionRates,
    getRecentSubscriptionResults
} from '../data/subscriptionData';
import { formatNumber, formatDate, formatCompactCurrency } from '../utils/formatters';

/**
 * 청약 정보 페이지
 * 청약 일정, 가점 계산기, 경쟁률 분석
 */
const SubscriptionInfo = memo(() => {
    const [activeTab, setActiveTab] = useState('schedule');
    const [selectedRegion, setSelectedRegion] = useState('all');

    // 가점 계산기 상태
    const [pointsInput, setPointsInput] = useState({
        noHouseYears: 5,
        dependents: 2,
        accountYears: 10,
    });

    // 데이터
    const schedules = useMemo(() => generateSubscriptionSchedule(), []);
    const competitionRates = useMemo(() => getRegionalCompetitionRates(), []);
    const recentResults = useMemo(() => getRecentSubscriptionResults(), []);

    // 지역 필터링
    const filteredSchedules = useMemo(() => {
        if (selectedRegion === 'all') return schedules;
        return schedules.filter(s => s.region === selectedRegion);
    }, [schedules, selectedRegion]);

    // 가점 계산
    const points = useMemo(() =>
        calculateSubscriptionPoints(pointsInput),
        [pointsInput]
    );

    // 통계
    const stats = useMemo(() => {
        const upcoming = schedules.filter(s => s.status === 'upcoming').length;
        const totalUnits = schedules.reduce((sum, s) => sum + s.totalUnits, 0);
        const avgCompetition = competitionRates.reduce((sum, r) => sum + r.rate, 0) / competitionRates.length;

        return { upcoming, totalUnits, avgCompetition };
    }, [schedules, competitionRates]);

    const tabs = [
        { id: 'schedule', label: '청약 일정', icon: Calendar },
        { id: 'calculator', label: '가점 계산기', icon: Calculator },
        { id: 'competition', label: '경쟁률 분석', icon: TrendingUp },
    ];

    const regions = ['all', '서울', '경기', '인천', '부산', '대구'];

    return (
        <div className="page-container">
            {/* Stats */}
            <div className="grid-stats" style={{ marginBottom: 24 }}>
                <StatCard
                    title="다가오는 청약"
                    value={stats.upcoming}
                    suffix="건"
                    subtitle="향후 60일 이내"
                    icon={Calendar}
                    iconColor="#6366f1"
                    iconBg="var(--color-primary-light)"
                />
                <StatCard
                    title="예정 공급물량"
                    value={formatNumber(stats.totalUnits)}
                    suffix="세대"
                    subtitle="총 공급 예정"
                    icon={Home}
                    iconColor="#10b981"
                    iconBg="#d1fae5"
                />
                <StatCard
                    title="평균 경쟁률"
                    value={stats.avgCompetition.toFixed(1)}
                    suffix=":1"
                    subtitle="수도권 기준"
                    icon={Users}
                    iconColor="#f59e0b"
                    iconBg="#fef3c7"
                />
                <StatCard
                    title="내 예상 가점"
                    value={points.totalPoints}
                    suffix={`/${points.maxPoints}`}
                    subtitle={`상위 ${100 - points.percentage}%`}
                    icon={Award}
                    iconColor="#8b5cf6"
                    iconBg="#ede9fe"
                />
            </div>

            {/* Tabs */}
            <Tabs
                tabs={tabs}
                activeTab={activeTab}
                onChange={setActiveTab}
                style={{ marginBottom: 24 }}
            />

            {/* Tab Content */}
            {activeTab === 'schedule' && (
                <div>
                    {/* Region Filter */}
                    <div className="card" style={{ marginBottom: 16, padding: '12px 16px' }}>
                        <div style={{ display: 'flex', alignItems: 'center', gap: 12 }}>
                            <Filter size={16} style={{ color: 'var(--color-text-tertiary)' }} />
                            <span style={{ fontSize: '0.85rem', color: 'var(--color-text-secondary)' }}>지역:</span>
                            <div style={{ display: 'flex', gap: 8 }}>
                                {regions.map(region => (
                                    <button
                                        key={region}
                                        onClick={() => setSelectedRegion(region)}
                                        className={`btn ${selectedRegion === region ? 'btn-primary' : 'btn-ghost'}`}
                                        style={{ padding: '6px 12px', fontSize: '0.8rem' }}
                                    >
                                        {region === 'all' ? '전체' : region}
                                    </button>
                                ))}
                            </div>
                        </div>
                    </div>

                    {/* Schedule List */}
                    <div style={{ display: 'flex', flexDirection: 'column', gap: 12 }}>
                        {filteredSchedules.map(schedule => (
                            <div
                                key={schedule.id}
                                className="card"
                                style={{
                                    display: 'flex',
                                    justifyContent: 'space-between',
                                    alignItems: 'center',
                                    padding: 16,
                                    cursor: 'pointer',
                                    transition: 'all var(--transition-fast)',
                                }}
                            >
                                <div style={{ display: 'flex', alignItems: 'center', gap: 16 }}>
                                    <div style={{
                                        width: 48,
                                        height: 48,
                                        borderRadius: 'var(--radius-md)',
                                        background: schedule.status === 'upcoming'
                                            ? 'var(--color-primary-light)'
                                            : 'var(--color-success-light)',
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'center',
                                    }}>
                                        <Home size={20} style={{
                                            color: schedule.status === 'upcoming'
                                                ? 'var(--color-primary)'
                                                : 'var(--color-success)'
                                        }} />
                                    </div>
                                    <div>
                                        <h4 style={{ fontWeight: 600, marginBottom: 4 }}>{schedule.name}</h4>
                                        <div style={{ display: 'flex', alignItems: 'center', gap: 8, fontSize: '0.8rem', color: 'var(--color-text-tertiary)' }}>
                                            <MapPin size={12} />
                                            <span>{schedule.address}</span>
                                            <span>•</span>
                                            <span>{schedule.totalUnits}세대</span>
                                        </div>
                                    </div>
                                </div>
                                <div style={{ display: 'flex', alignItems: 'center', gap: 24 }}>
                                    <div style={{ textAlign: 'right' }}>
                                        <p style={{ fontSize: '0.75rem', color: 'var(--color-text-tertiary)' }}>청약기간</p>
                                        <p style={{ fontSize: '0.85rem', fontWeight: 500 }}>
                                            {formatDate(schedule.startDate, 'short')} ~ {formatDate(schedule.endDate, 'short')}
                                        </p>
                                    </div>
                                    <div style={{ textAlign: 'right' }}>
                                        <p style={{ fontSize: '0.75rem', color: 'var(--color-text-tertiary)' }}>평균 분양가</p>
                                        <p style={{ fontSize: '0.85rem', fontWeight: 600, color: 'var(--color-primary)' }}>
                                            {formatCompactCurrency(schedule.avgPrice)}
                                        </p>
                                    </div>
                                    <ChevronRight size={20} style={{ color: 'var(--color-text-tertiary)' }} />
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            )}

            {activeTab === 'calculator' && (
                <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: 24 }}>
                    {/* Input */}
                    <div className="card">
                        <h3 style={{ fontSize: '1rem', fontWeight: 600, marginBottom: 20 }}>가점 항목 입력</h3>

                        <div className="form-group">
                            <label>무주택 기간 (년)</label>
                            <input
                                type="number"
                                value={pointsInput.noHouseYears}
                                onChange={(e) => setPointsInput(prev => ({ ...prev, noHouseYears: Number(e.target.value) }))}
                                min="0"
                                max="30"
                            />
                            <span style={{ fontSize: '0.75rem', color: 'var(--color-text-tertiary)' }}>
                                만 30세 이상부터 인정, 최대 15년 이상
                            </span>
                        </div>

                        <div className="form-group">
                            <label>부양가족 수 (본인 제외)</label>
                            <input
                                type="number"
                                value={pointsInput.dependents}
                                onChange={(e) => setPointsInput(prev => ({ ...prev, dependents: Number(e.target.value) }))}
                                min="0"
                                max="6"
                            />
                            <span style={{ fontSize: '0.75rem', color: 'var(--color-text-tertiary)' }}>
                                배우자, 직계존비속 포함
                            </span>
                        </div>

                        <div className="form-group">
                            <label>청약통장 가입기간 (년)</label>
                            <input
                                type="number"
                                value={pointsInput.accountYears}
                                onChange={(e) => setPointsInput(prev => ({ ...prev, accountYears: Number(e.target.value) }))}
                                min="0"
                                max="20"
                            />
                            <span style={{ fontSize: '0.75rem', color: 'var(--color-text-tertiary)' }}>
                                청약저축 또는 청약예금 가입일 기준
                            </span>
                        </div>
                    </div>

                    {/* Result */}
                    <div className="card" style={{
                        background: 'linear-gradient(135deg, var(--color-primary), #7c3aed)',
                        color: 'white',
                        border: 'none',
                    }}>
                        <h3 style={{ fontSize: '1rem', fontWeight: 600, marginBottom: 20, opacity: 0.9 }}>
                            가점 계산 결과
                        </h3>

                        <div style={{ textAlign: 'center', marginBottom: 24 }}>
                            <div style={{ fontSize: '4rem', fontWeight: 700 }}>
                                {points.totalPoints}
                            </div>
                            <div style={{ opacity: 0.8 }}>/ {points.maxPoints}점 (상위 {100 - points.percentage}%)</div>
                        </div>

                        <div style={{ display: 'flex', flexDirection: 'column', gap: 12 }}>
                            <div style={{ display: 'flex', justifyContent: 'space-between', padding: '12px', background: 'rgba(255,255,255,0.15)', borderRadius: 'var(--radius-md)' }}>
                                <span>무주택 기간</span>
                                <span style={{ fontWeight: 600 }}>{points.noHousePoints}점</span>
                            </div>
                            <div style={{ display: 'flex', justifyContent: 'space-between', padding: '12px', background: 'rgba(255,255,255,0.15)', borderRadius: 'var(--radius-md)' }}>
                                <span>부양가족 수</span>
                                <span style={{ fontWeight: 600 }}>{points.dependentPoints}점</span>
                            </div>
                            <div style={{ display: 'flex', justifyContent: 'space-between', padding: '12px', background: 'rgba(255,255,255,0.15)', borderRadius: 'var(--radius-md)' }}>
                                <span>청약통장 기간</span>
                                <span style={{ fontWeight: 600 }}>{points.accountPoints}점</span>
                            </div>
                        </div>
                    </div>
                </div>
            )}

            {activeTab === 'competition' && (
                <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: 24 }}>
                    {/* Competition Chart */}
                    <ChartContainer title="지역별 평균 경쟁률" height={400}>
                        <ResponsiveContainer width="100%" height="100%">
                            <BarChart data={competitionRates} layout="vertical" margin={{ left: 80 }}>
                                <CartesianGrid strokeDasharray="3 3" stroke="var(--color-border)" />
                                <XAxis type="number" tick={{ fontSize: 11 }} />
                                <YAxis type="category" dataKey="region" tick={{ fontSize: 12 }} width={70} />
                                <Tooltip
                                    contentStyle={{
                                        background: 'var(--color-bg-secondary)',
                                        border: '1px solid var(--color-border)',
                                        borderRadius: 'var(--radius-md)',
                                    }}
                                    formatter={(value) => [`${value}:1`, '경쟁률']}
                                />
                                <Bar dataKey="rate" radius={[0, 4, 4, 0]}>
                                    {competitionRates.map((entry, index) => (
                                        <Cell
                                            key={index}
                                            fill={entry.rate > 30 ? '#ef4444' : entry.rate > 15 ? '#f59e0b' : '#10b981'}
                                        />
                                    ))}
                                </Bar>
                            </BarChart>
                        </ResponsiveContainer>
                    </ChartContainer>

                    {/* Recent Results */}
                    <div className="card">
                        <h3 style={{ fontSize: '1rem', fontWeight: 600, marginBottom: 16 }}>최근 청약 결과</h3>
                        <div style={{ display: 'flex', flexDirection: 'column', gap: 12 }}>
                            {recentResults.map((result, index) => (
                                <div
                                    key={index}
                                    style={{
                                        padding: 16,
                                        background: 'var(--color-bg-tertiary)',
                                        borderRadius: 'var(--radius-md)',
                                    }}
                                >
                                    <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: 8 }}>
                                        <span style={{ fontWeight: 600 }}>{result.name}</span>
                                        <span style={{ fontSize: '0.8rem', color: 'var(--color-text-tertiary)' }}>
                                            {formatDate(result.date, 'short')}
                                        </span>
                                    </div>
                                    <div style={{ fontSize: '0.85rem', color: 'var(--color-text-secondary)', marginBottom: 8 }}>
                                        {result.region}
                                    </div>
                                    <div style={{ display: 'flex', gap: 16, fontSize: '0.8rem' }}>
                                        <span>경쟁률: <strong style={{ color: 'var(--color-danger)' }}>{result.competition}:1</strong></span>
                                        <span>커트라인: <strong>{result.cutoffPoints}점</strong></span>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
});

SubscriptionInfo.displayName = 'SubscriptionInfo';

export default SubscriptionInfo;
